.btn {
   padding: 4px;
   text-align: center;
   width: 100px;
   border-radius: 80px;
   transition: all 0.3s ease 0s;
   background: $primaryColor;
   color: $mainColor;
   display: inline-block;

   &:hover {
      background: #FFE302;
   }
}

.title {
   font-size: 40px;
   line-height: 40px;
}